<template>
  <div id="container" class="fade-in">
    <div id="auto_div">
      <input type="checkbox" v-model="auto_login" @click="setAuto" id="auto_login" />
      <span style="margin-left:5px; color:red;">auto login</span>
    </div>
    <h6 id="action_label">Please log in or register to continue</h6>
    <div id="login" class="mt-3">
      <h5>Login</h5>
      <form id="form_login">
        <input type="email" id="login_email" placeholder="email" class="mb-2" v-model="login_email" required autofocus>
        <input type="password" id="login_password" placeholder="password" v-model="login_password">
        <h6 class="errorMsg" v-if="login_error">{{ login_error }}</h6>
        <button id="login_btn" class="btn btn-primary mt-2 mb-2" @click="login">Log In</button>
      </form>
    </div>
    <div id="register" class="mt-3">
      <h5>Register</h5>
      <form id="form_register">
        <input type="text" class="mb-2" v-model="username" placeholder="name">
        <input type="email" id="register_email" placeholder="email" class="mb-2" v-model="email">
        <input type="password" id="register_password" placeholder="password" v-model="password">
        <h6 class="errorMsg" v-if="register_error">{{ register_error }}</h6>
        <button id="register_btn" class="btn btn-success mt-2 mb-2" @click="register">Register</button>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "@/config/db";
//import { auth } from "@/config/auth"

export default {
  name: "auth",
  data() {
    return {
      username: "",
      login_email: "",
      login_password: "",
      email: "",
      password: "",
      login_error: "",
      register_error: "",
      db: db,
      auto_login: null
    };
  },
  methods: {
    setAuto() {
      this.auto_login = !this.auto_login;
      if (this.auto_login) {
        localStorage.setItem("myorganiser_auto", true);
      } else {
        localStorage.setItem("myorganiser_auto", false);
        localStorage.setItem("myorganiser_login_email", "");
        localStorage.setItem("myorganiser_login_password", "");
      }
    },
    validateEmail(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    login() {
      this.login_error = "";
      if (this.login_email === "" || this.login_password === "") {
        this.login_error = "email and password required";
      } else {
        if (!this.validateEmail(this.login_email)) {
          this.login_error = "badly formed email";
          return;
        } else {
          if (!this.$store.getters.getJustLoggedOut) {
            firebase
              .auth()
                .signInWithEmailAndPassword(this.login_email, this.login_password)
                  .then(() => {
                    let user = firebase.auth().currentUser;
                    localStorage.setItem("myorganiser_userId", user.uid);
                    this.$store.commit("setStatus", true);
                    this.$router.push("/");
                    if (this.auto_login) {
                      localStorage.setItem(
                        "myorganiser_login_email",
                        this.login_email
                      );
                      localStorage.setItem(
                        "myorganiser_login_password",
                        this.login_password
                      );
                    }
                    this.$store.commit("setJustLoggedOut", false);
                  })
          }
        }
      }
    },
    register() {
      this.register_error = "";
      if (this.email === "" || this.password === "" || this.username === "") {
        this.register_error = "name, email and password required";
      } else {
        if (!this.validateEmail(this.email)) {
          this.register_error = "badly formed email";
          return;
        } else {
          firebase
            .auth()
            .createUserWithEmailAndPassword(this.email, this.password)
            .then(() => {
              this.$store.commit("setStatus", true);
              this.$router.push("/");
            });
        }
      }
    }
  },
  mounted() {
    this.auto_login = localStorage.getItem("myorganiser_auto");

    if (this.auto_login && !this.$store.getters.getLoggedOut) {
      let email = localStorage.getItem("myorganiser_login_email");
      let pword = localStorage.getItem("myorganiser_login_password");

      firebase
        .auth()
        .signInWithEmailAndPassword(email, pword)
        .then(() => {
          var user = firebase.auth().currentUser;
          localStorage.setItem("myorganiser_userId", user.uid);
          this.$store.commit("setStatus", true);
        })
        .catch(err => {
          console.log(err.message);
        });
    }
  }
};
</script>

