<template>
  <div class="home">
    <h1 id="main_title">Pocket Office</h1>
    <i class="fas fa-sign-out-alt" id="logout_link" style="font-size:1.5rem; width:90%;" v-if="this.$store.getters.getLoggedStatus" @click="logout"></i>

    <Auth class="col-sm-12" v-if="this.$store.getters.getLoggedStatus == false"></Auth>
    <div class="menu-links scale-up-ver-center" v-else>
      <router-link to='Appointments'><button class="link-btn"><i class="fas fa-calendar-check"/>Appointments</button></router-link>
      <router-link to='Diary'><button class="link-btn"><i class="fas fa-book"/>Diary</button></router-link>
      <router-link to='Lists'><button class="link-btn"><i class="fas fa-list"/>Lists</button></router-link>
      <router-link to='Meetings'><button class="link-btn"><i class="fas fa-handshake"/>Meetings</button></router-link>
      <router-link to='Notes'><button class="link-btn"><i class="fas fa-book-open"/>Notes</button></router-link>
    </div>
    <div v-if="!this.$store.getters.getLoggedStatus">
      <h3 id="focus_text" class="text-center mt-4 heartbeat">Your one stop office.. in your pocket</h3>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Auth from "@/components/Auth";
import firebase from "firebase";

export default {
  name: "home",
  data() {
    return {
      showAuth: true
    };
  },
  components: { Auth },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          localStorage.setItem("myorganiser_userid", "");
          this.$store.commit("setStatus", false);
          this.$store.commit("setJustLoggedOut", true);
        })
        .catch(err => {
          console.log(err.message);
        });
    }
  },
  created() {}
};
</script>

